html{
    scroll-behavior: smooth;
}

body{
    font-family: 'Open Sans', sans-serif;
    color: #333;
}

h1{
    font-size: 48px;
    font-weight: bold;
}

h2{
    font-size: 36px;
    font-weight: bold;
}

h3{
    font-size: 21px;
    font-weight: bold; 
}

p{
    font-size: 18px;
    font-weight: normal;
    line-height: 2;
}

li{
    font-size: 18px;
    font-weight: normal;
    line-height: 2;
}

body{
    background-color: #fafafa;
}
header{
    color: #333;
}

.navFather{
    display: flex;
    justify-content: space-between;
}

.navChild-A{
    margin: 30px;
    cursor: pointer;
} 

.navChild-B{
    display: flex;
} 

.child-A{
    color: #333;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 20px;
    cursor: pointer;
} 

.child-B{
    color: #333;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 40px;
    cursor: pointer;
} 
.heroFather{
    width: 100%;
    display: flex;
    flex-direction: column;
    color: #fff;
}

.heroMsj{
    background-image: url(/react-shop/static/media/hero.ec2e862a.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
}

.hero-Txt{
    margin: 300px 0;
    cursor: pointer;
    box-shadow: 0px 0px 10px 5px #9c9c9c14;
}

.hero-Txt:hover{
    color: #eee;
}

.heroButton{
    display: flex;
}

.heroButton-A{
    background-color: #CDB79B;
    padding: 40px 0;
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.heroButton-A:hover{
    background-color: #ac9d89;
}

.heroButton-B{
    background-color: #525B66;
    padding: 40px 0;
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.heroButton-B:hover{
    background-color: rgb(54, 60, 66);
}

.all{
    display: flex;
    justify-content: center;
}

.allTxt{
    color: #333;
    margin: 40px 0;
    cursor: pointer;
}

.categories{
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}
.category{
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 5px;
    padding: 30px 20px;
    min-width: 80px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 5px #9c9c9c14;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.category:hover{
    background-color: #7fc4f1;
    color: #fff;
    cursor: pointer;
    box-shadow: none;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.category:active{
    background-color: #449cd6;
}

.icon{
    width: 40px;
    margin: 0 0 10px 0;
}


.name{
    font-size: 16px;
}


.list{
    background-color: #fafafa;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}

.list-filter{
    background-color: #fafafa;
    width: 15%;
}

.list-product{
    background-color: #fafafa;
    margin: 0 0 0 40px;
    display: grid;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    grid-template-columns: auto auto auto auto;
}
.product{
    background-color: #ffffff;
    min-width: 250px;
    max-width: 250px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border: 0.5px solid #ffffff;

    /* -webkit-box-shadow: 0px 0px 10px 5px #9c9c9c14;
    -moz-box-shadow: 0px 0px 10px 5px #9c9c9c14;
    box-shadow: 0px 0px 10px 5px #9c9c9c14; */
}

.product:hover{
}

.product-img{
    background-color: #ffffff;
    width: 200px;
    height: 250px;
}

.product-name{
    font-size: 15px;
    font-weight: 600;
    padding: 10px 0 0 0;
}

.product-price{
    color: #861f1f;
    font-size: 15px;
    font-weight: 600;
    padding: 10px 0 0 0;
}

.product-brand{
    font-size: 14px;
    font-weight: 400;
    padding: 10px 0 0 0;
}

.product-btn{
    background-color: #f6f6f6;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    display: inline-block;

    cursor: pointer;
    width: 200px;
    height: 50px;
    margin: 10px 0 0 0;
}

.product-btn:hover{
    background-color: #7fc4f1;
    color: #fff;
}

.product-btn:active{
    background-color: #449cd6;
}


.filter-section{
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin: 0 0 10px 0;
    box-shadow: 0px 0px 10px 5px #9c9c9c14;
}

.filter-name{
    padding: 20px;
}

.filter-option{
    padding: 10px 20px 20px 20px;
    font-size: 16px;
    color: #999;
}
.filter-option:last-child{
    padding: 5px 20px 20px 20px;
    font-size: 16px;
}

.filter-option:hover{
    background-color: #7fc4f1;
    cursor: pointer;
    color: #fff;
}

.filter-option:active{
    background-color: #449cd6;
}

/*-------------------- COLOR */
.color-section{
    display: flex;
    justify-content: center;
}

.color{
    width: 40px;
    height: 40px;
    border: 0.5px solid #aaa;
    margin: 5px 5px 20px 5px;
    background-color: #fff;
}

.color:hover{
    cursor: pointer;
}

.black{ background-color: #313131;}
.white{ background-color: #ffffff;}
.red{ background-color: #7c1e1e;}
.green{ background-color: #3c663c;}
.blue{ background-color: #2f4a7c;}

/*-------------------- TALLA */ 
.size-section{
    display: flex;
    justify-content: center;
}

.size{
    width: 40px;
    height: 40px;
    border: 0.5px solid #aaa;
    margin: 5px 5px 20px 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.size:hover{
    background-color: #7fc4f1;
    cursor: pointer;
    color: #fff;
}

.size:active{
    background-color: #449cd6;
    cursor: pointer;
}

/*-------------------- PRECIO */ 
.price-section{
    display: flex;
    justify-content: center;
}

.price-min{
    width: 80px;
    height: 30px;
    margin: 0 10px 20px 10px;
}

.price-max{
    width: 80px;
    height: 30px;
    margin: 0 10px 20px 10px;
}
.pages{
    display: flex;
    justify-content: center;
    margin-bottom: 100px;
}

.pages-number{
    width: 50px;
    height: 50px;
    margin: 5px;
    background-color: #fff;
    box-shadow: 0px 0px 10px 5px #9c9c9c14;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pages-number:hover{
    background-color: #7fc4f1;
    cursor: pointer;
    color: #fff;
}

.pages-number:active{
    background-color: #449cd6;
    box-shadow: none;
}
